'use strict'

const test = require('ava')
const senagram = require('../')
// const fixtures = require('./fixtures')

test('cliente', t => {
  const client = senagram.crearCliente()

  t.is(client.getPicture, 'function')
  t.is(client.guardarPicture, 'function')
  t.is(client.likePicture, 'function')
  t.is(client.listaPicture, 'function')
  t.is(client.listaPicturePorTag, 'function')
  t.is(client.guardarUsuario, 'function')
  t.is(client.getUsuario, 'function')
  t.is(client.autenticar, 'function')
})
